<template>
  <div>
    <router-link to="/admin/add">
    <el-button type="primary">
        <i class="el-icon-circle-plus-outline"></i>添加
    </el-button>
    </router-link>
    <div class="bgf">
        <!-- 列表 -->
        <el-table :data="list" >
            <el-table-column width="300px" prop="uid" label="用户ID" ></el-table-column>
            <el-table-column prop="username" label="用户名" ></el-table-column>
            <el-table-column prop="rolename" label="角色名称" ></el-table-column>
            <el-table-column prop="status" label="状态" >
                <template slot-scope="scope">
                    <el-tag type="success" v-if="scope.row.status">已启用</el-tag>
                    <el-tag type="danger" v-else>已禁用</el-tag>  
                </template>
            </el-table-column>
            <el-table-column label="操作"  align="center">
                <template slot-scope="scope">
                      <router-link :to="'/admin/'+scope.row.uid"><el-button type="success" icon="el-icon-edit" size="mini" circle  ></el-button></router-link>
                      <el-button type="danger" icon="el-icon-delete" size="mini" @click="delRow(scope.row.uid)" circle></el-button>
                </template>
            </el-table-column>
        </el-table>
        <br>
        <!-- 分页 -->
        <el-pagination
            background
            :page-size="size"
            @current-change="pagechange"
            :current-page="page"
            :total="total">
        </el-pagination>
    </div>
  </div>
</template>

<script>
// 获取数据、删除数据
import { getAdmin,delAdmin,getTotal } from "@/request/admin"
export default {
    data(){
        return{
            list:[],
            total:0,  // 总数
            size:2,  // 每页条数
            page:1    // 页码数
        }
    },
    created(){
        this.getlist();  // 获取列表
    },
    methods:{
        async getlist(){
            this.list = await getAdmin({size:this.size,page:this.page}); 
            this.total = await getTotal(); 
        },
        delRow(uid){  // 删除某一条数据，接收删除的ID
            // 弹框确认！
            this.$confirm('确定删除吗, 是否继续?', '提示', {
                confirmButtonText: '确定',
                cancelButtonText: '取消',
                type: 'warning'
            }).then(async () => {
                // 删除功能！
                await delAdmin(uid);
                // 当前页数据只有一条的数据，删除成功之后，应该让页码退1，请求上一页的数据。
                if(this.list.length==1 && this.page!=1){
                    this.page--;
                }
                this.getlist();
            })
        },
        pagechange(val){
            this.page = val; // val就是新的页码
            this.getlist();  //  获取新的列表
        }
    },
    components:{
    }
}

</script>
<style scoped>
</style>


